@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

:root {
  --icons-color: #4c5773;
  --icons-light-color: #4c577341;
  --icons-bg-color: #e2e6e9;
  --shadow-dark-color: #d3dae7;
  --shadow-light-color: #fff;
  --main-bg-color: #ecf0f3;
  /* --main-bg-color: #f5f5f5; */

  --box-shadow: 1rem 1rem 1rem var(--shadow-dark-color),
    -1rem -1rem 1rem var(--shadow-light-color);

  --box-shadow-2: 0rem 0rem 0rem var(--shadow-dar`k-color),
    -0.3rem -0.3rem 1rem var(--shadow-dark-color);
}

html,
body {
  padding: 0;
  margin: 0;
  font-family: "Poppins", sans-serif;
  font-size: 14px;
  /* background-color: var(--main-bg-color); */
  color: var(--icons-color);
  background-image: linear-gradient(
    240deg,
    rgba(166, 239, 253, 1) 5%,
    white 50%
  );
}

a {
  color: inherit;
  text-decoration: none;
}

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: none;
}

::selection {
  color: var(--shadow-dark-color);
  background: var(--icons-color);
}

::-webkit-scrollbar {
  width: 1rem;
  border: 1px solid var(--main-bg-color);
}

::-webkit-scrollbar-track {
  border-radius: 0;
  background-color: var(--main-bg-color);
}

::-webkit-scrollbar-thumb {
  border-radius: 2rem;
  background: var(--icons-color);
  width: 0.5rem;
}
@media (prefers-color-scheme: dark) {
  html {
    color-scheme: dark;
  }
  body {
    color: white;
    background: black;
  }
}
